@import "../../global.scss";

.top{
    width: 100%;
    height: 70px;
    background-color: $mainColor;
    color:#5252ff;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    
        .left{
            display: flex;
            align-items: center;
            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }
    
            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 30px;
    
                .icon{
                    font-size: 18px;
                    margin-right: 4px;
                }
    
                span{
                    font-size: 20px;
                    font-weight: 300;
                }
            }
        }
    
        .right{
    
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
    
                span{
                    width: 100%;
                    height: 5px;
                    background-color: #5252ff;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    &.active{
        background-color: #5252ff;
        color: $mainColor;

        .hamburger{
           span{
               &:first-child{
                   background-color:  white;
                   transform: rotate(45deg);
               }
               &:nth-child(2){
                   opacity: 0;
               }
               &:last-child{
                   background-color: white;
                   transform: rotate(-45deg);
               }
           } 
        }
    }
}

